body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 100vh;
}

.AppHeader {
  background-color: #f1f1f1;
  overflow: visible;
  width: 100%;
  text-align: center;
  color: #8f8f8f;
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
}

*:focus {
  outline: none;
}

/* html {
  overflow: scroll;
  overflow-x: hidden;
}
::-webkit-scrollbar {
  width: 0px;
  background: transparent; 
} */

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(207, 207, 207); 
}
form {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 0.5rem auto 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

input {
  padding: 5px;
  font-size: 16px;
  border-radius: 8px;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  border: none;
  background-color: #e2e2e2;
  color: black;
}

input ::-webkit-input-placeholder {
  color: #8f8f8f;
}

input ::-ms-input-placeholder {
  color: #8f8f8f;
}

input ::placeholder {
  color: #8f8f8f;
}

button {
  padding: 5px 10px;
  font-size: 16px;
  background-color: #737cff;
  color: white;
  border: none;
  border-radius: 8px;
  margin-left: 10px;
}
.MessagesList {
  padding: 20px 0;
  width: 100%;
  margin: 0 auto;
  list-style: none;
  padding-left: 0;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  overflow: auto;
}

.Message {
  display: -webkit-flex;
  display: flex;
  margin-top: 30px;
}

.CurrentUser {
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
  text-align: right;
}

.MessageContent {
  display: inline-block;
  padding-left: 1rem;
  padding-right: 1rem;
}

.CurrentUser > .MessageContent {
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.CurrentUser > .MessageContent .MessageText {
  background-color: lightcoral;
}

.UserColour {
  height: 35px;
  width: 35px;
  border-radius: 50%;
  display: inline-block;
  margin: 0 10px -10px;
  -webkit-flex: 0 0 35px;
          flex: 0 0 35px;
}

.MessageContent > .Username {
  display: block;
  color: gray;
  font-size: 14px;
  padding-bottom: 4px;
}

.MessageContent > .MessageText {
  padding: 10px;
  max-width: 400px;
  margin: 0;
  border-radius: 12px;
  background-color: lightblue;
  color: white;
  display: inline-block;
}

.MessageContent > .MessageTime {
  display: block;
  color: grey;
  font-size: 14px;
  padding-bottom: 4px;
}
